/**/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
QACheckDetailModalTempl = require('./QACheckDetailModal.html'),
  func = require('func');

QACheckDetailModalCtrl.$inject = ['modalService'];

function QACheckDetailModalCtrl(modalService) {
  var vm = this;

  vm.content = {};    //

  /**/
  vm.openQACheckDetailModal = openQACheckDetailModal;

  /**/
  function openQACheckDetailModal(content) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '';
    options.template = QACheckDetailModalTempl;
    options.controller = defaultController;
    vm.content = content;
    modalService.openModal(options);
  }

  /*controller*/
  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.submitText = '';
    ra.QAData = vm.content;
    console.log('data', ra.QAData);

    ra.cancel = cancel;
    ra.close = close;

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = QACheckDetailModalCtrl;



