/* name module */

var modalOptions = require('jsDir/modal_options');

httpInterceptor.$inject = ['$rootScope', '$q', '$cookies', '$injector', 'modalService'];

//$injector.get$uibModalJSui.bootstrap
//$injector
function httpInterceptor($rootScope, $q, $cookies, $injector, modalService) {
  var interceptor = {
    /**
     * request 
     *  $http 
     *  $http  config  config 
     * 
     * @param config
     * @returns {*}
     */
    request: function(config) {
      return config;
    },

    /**
     * requestError 
     *  Interceptor  requestError  Interceptor
     *  $q.reject() 
     * @param err
     * @returns {Promise}
     */
    requestError: function(err) {
      return $q.reject(err);
    },

    /**
     * response 
     *  $http 
     * 
     *  API HTTP  200 
     * 
     * @param res
     * @returns {*}
     */
    response: function(res) {
      var data = res && res.data;
      var options = angular.copy(modalOptions);
      if (angular.isObject(data)) {
        if (data['errorCode'] == 'E000015' || data['errorCode'] == 'E000017') {
          // tokentoken
          sessionStorage.removeItem('access_token');
          // 
          sessionStorage.removeItem('currentUrl');
          $rootScope = $injector.get('$rootScope');
          // view
          $rootScope.$emit('userIntercepted', 'sessionOut', data);
          //
          $state = $injector.get('$state');
          options.modalInfo = '';
          options.modalTitle = '';
          options.modalPriority = 'unique';
          options.confirmCallback = function(result) {
            //
            $state.go('login');
          };
          options.cancelCallback = function(result) {
            //
            $state.go('login');
          };
          modalService.openModal(options);
          console.log('httpInterceptor  userIntercepted');
        }
      }
      return res;
    },

    /**
     * responseError 
     * @param err
     * @returns {*|Promise.<*>|Promise}
     */

    responseError: function(err) {
      console.log('responseError' + JSON.stringify(err));

      var _modalOptions = angular.copy(modalOptions);
      var uibModalStack = $injector.get('$uibModalStack');

      //3
      //_modalOptions.modalTime = 1500;
      switch (err.status) {
        case -1:
          _modalOptions.modalInfo = ',';
          break;
        case 500: {
          _modalOptions.modalInfo = '500';
          break;
        }
        case 404: {
          if (err.config.method == 'JSONP') {
            return $q.reject(err);
          }
          _modalOptions.modalInfo = '404';
          break;
        }
        case 501: {
          _modalOptions.modalInfo = '501';
          break;
        }
        case 502: {
          _modalOptions.modalInfo = '502';
          break;
        }
        case 400: {
          //_modalOptions.modalInfo = '';
          return $q.reject(err);
        }
        case 401: {
          _modalOptions.modalInfo = '';
          break;
        }
      }

      modalService.openModal(_modalOptions);
      uibModalStack.dismissAll();

      return $q.reject(err);
    }
  };

  return interceptor;
}

module.exports = httpInterceptor;



