require('./style.less');

//
var Routing = require('./route');

// servicecontrollerdirective
var ctrl = require('./controller');
var service = require('./service');
var addRiskLevelModalCtrl = require('./modalPopup/addRiskLevel/controller');
var addRiskLevelModalService = require('./modalPopup/addRiskLevel/service');

//setter
module.exports = angular
  .module('riskLevelSetting', [])
  .config(Routing)
  .controller('riskLevelSettingCtrl', ctrl)
  .service('riskLevelSettingService', service)
  .factory('addRiskLevelModalCtrl', addRiskLevelModalCtrl)
  .service('addRiskLevelModalService', addRiskLevelModalService).name;



