 // 

require('locale_zh');

var func = require('func');
var passImg = require('img/already-pass.png');
var noPassImg = require('img/no-pass.png');

servePackageCheckCtrl.$inject = ['modalService', 'servePackageCheckService', 'packageDetailModalCtrl', '$sce', '$timeout'];

function servePackageCheckCtrl(modalService, servePackageCheckService, packageDetailModalCtrl, $sce, $timeout) {
  var vm = this;

  vm.passImg = passImg;   //
  vm.noPassImg = noPassImg;   //

  vm.tenantId = JSON.parse(sessionStorage.getItem("tenantId"));
  vm.operatorNo = JSON.parse(sessionStorage.getItem("operatorInfo")).operatorNo;
  vm.operatorName = JSON.parse(sessionStorage.getItem("operatorInfo")).name;

  /*  */
  vm.maxSize = 10;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  //  
  vm.auditType = null; // 
  vm.typeList = [{"code": "", "value": ""}, {"code": "0", "value": ""}, {"code": "1", "value": ""}, {"code": "2", "value": ""}];
  vm.clickAble = 0; // 012
  vm.checkOKText = $sce.trustAsHtml(''); // 
  vm.checkNOText = $sce.trustAsHtml(''); // 

  vm.keywords = '';
  vm.list = [];
  vm.selectedIndex = 0; // 
  vm.productData = {};
  vm.productDetailData = {};
  vm.productPrice = 0;
  vm.auditOpinion = '';

  vm.auditCheckSearch = auditCheckSearch;
  vm.openModal = openModal;
  vm.pageChangeFunc = pageChangeFunc;
  vm.chooseAuditHis = chooseAuditHis;
  vm.searchByKeyword = searchByKeyword;
  vm.productCheckFunc = productCheckFunc;

  getListFunc();

  // 
  function auditCheckSearch(type) {
    vm.currentPage = 1;
    vm.auditType = type;
    getListFunc();
  }
  
  // 
  function getListFunc() {
    servePackageCheckService.productListQry({
      tenant_id: vm.tenantId,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage,
      product_name: vm.keywords,
      audit_states: vm.auditType
    }, function(res) {
      console.log('res', res);
      vm.list = res.data_list;
      vm.totalItems = res.total_num || 0;
      showProductDetailsFunc(0);
    }, function(err) {
      console.log('err', err);
    })
  }

  // 
  function getDetailFunc() {
    servePackageCheckService.productDetailQry({
      show_type: '1',
      tenant_id: vm.tenantId,
      product_ids: vm.productData.product_id || ''
    }, function(res) {
      console.log('res', res);
      vm.productDetailData = res.data_list[0];
      vm.productPrice = (JSON.parse(vm.productDetailData.product_price)[0].price / 100).toFixed(2);
    }, function(err) {
      console.log('err', err);
    })
  }

  // 
  function productCheckFunc(type) {
    if (!vm.clickAble) {
      vm.clickAble = type;
      if (type == 1) {
        vm.checkOKText = $sce.trustAsHtml('<i class="icon iconloading turn-around"></i>');
      } else if (type == 2) {
        vm.checkNOText = $sce.trustAsHtml('<i class="icon iconloading turn-around"></i>')
      }
      servePackageCheckService.productApproval({
        tenant_id: vm.tenantId,
        product_ids: vm.productData.product_id || '',
        operate_content: type, // 1-2-
        operator_no: vm.operatorNo,
        operator_name: vm.operatorName,
        remark: vm.auditOpinion
      }, function(res) {
        console.log('res', res);
        modalService.openAlert('', res.error_info);
        $timeout(function() {
          vm.clickAble = 0;
          if (type == 1) {
            vm.checkOKText = $sce.trustAsHtml('');
          } else if (type == 2) {
            vm.checkNOText = $sce.trustAsHtml('');
          }
          $scope.$apply();
        })
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('', err.error_info);
        $timeout(function() {
          vm.clickAble = 0;
          if (type == 1) {
            vm.checkOKText = $sce.trustAsHtml('');
          } else if (type == 2) {
            vm.checkNOText = $sce.trustAsHtml('');
          }
          $scope.$apply();
        })
      })
    }
  }

  // 
  function showProductDetailsFunc(index) {
    vm.selectedIndex = index;
    vm.productData = vm.list[index];
    vm.auditOpinion = '';
    getDetailFunc();
  }

  //
  function chooseAuditHis() {
    vm.auditOpinion = vm.productData.remark;
  }

  function openModal() {
    packageDetailModalCtrl.openPackageDetailModalFunc();
  }

  function searchByKeyword() {
    vm.currentPage = 1;
    getListFunc();
  }

  function pageChangeFunc() {
    getListFunc();
  }
}

module.exports = servePackageCheckCtrl; 


