/*  module */

require('locale_zh');
var func = require('func'),
  modalOptions = require('jsDir/modal_options');

recommendCombineRuleCtrl.$inject = ['recommendCombineRuleService', 'modalService'];

function recommendCombineRuleCtrl(recommendCombineRuleService, modalService) {
  var vm = this;

  /*  */
  vm.maxSize = 10;
  vm.totalItems = 1;
  vm.currentPage = 1;
  vm.itemsPerPage = 10;
  vm.start = 0; //
  vm.end = 10; //

  vm.companyId = JSON.parse(sessionStorage.getItem('packHead')).company_id;
  vm.recomendRadio = ''; //0-7-5-
  vm.showRule = true; //
  vm.selectedCombi = []; //combinedom
  vm.selectedCombiId = []; //combiId

  vm.combiName = ''; //
  vm.combiCode = ''; //

  vm.pageAdviserList = []; //
  vm.adviserList = []; //
  vm.recommendedUserId = []; //userId
  vm.adviserId = ''; //

  vm.cancelNum = 0; //
  vm.recommendNum = 0; //

  vm.status = [{ code: '0', value: '' }, { code: '1', value: '' }, { code: '2', value: '' }, { code: '3', value: '' }, { code: '4', value: '' }];
  vm.dict = {
    investStyle: vm.status
  };

  vm.changeShowRule = changeShowRule; //
  vm.pageChangeFunc = pageChangeFunc; //
  vm.adviserInfoQry = adviserQry; //
  vm.publishRule = publishRule; //
  // vm.selectAdviser = selectAdviser; //
  vm.upHotCombine = upHotCombine; // 
  vm.deleteAdviser = deleteAdviser; //

  getRecommendRule(); //
  adviserQry(); //
  getRecommendedCombi(); // 

  //
  function getRecommendRule() {
    recommendCombineRuleService.recommendRuleQry(
      {
        company_id: vm.companyId,
        recommend_biz_type: '2', //1-2-3-
      },
      function(res) {
        //0-7-5-
        vm.recomendRadio = res.recommend_order_type || '5';
        changeShowRule(vm.recomendRadio);
      }
    )
  }

  //
  function adviserQry() {
    recommendCombineRuleService.recommendCombiInfoQry(
      {
        company_id: vm.companyId,
        request_num: 10000,
        position_str: 0,
        combi_code: vm.combiCode,
        combi_name: vm.combiName
      },
      function(result) {
        vm.adviserList = result.data_list;
        vm.totalItems = vm.adviserList.length;
        for (var k = 0; k < vm.totalItems; k++) {
          for (var t = 0; t < 5; t++) {
            if (vm.adviserList[k].invest_style == vm.status[t].code) {
              vm.adviserList[k].invest_style = vm.status[t].value;
            }
          }
        }
        pageChangeFunc();
      }
    );
  }

  //
  function changeShowRule(flag) {
    if (flag == '5') {
      vm.showRule = true;
    } else {
      vm.showRule = false;
    }
  }

  //
  function pageChangeFunc() {
    vm.start = (vm.currentPage - 1) * 10;
    vm.end = vm.start + vm.itemsPerPage;
    vm.pageAdviserList = vm.adviserList.slice(vm.start, vm.end);
  }

  //
  function publishRule() {
    if (vm.recomendRadio == '5') {
      if (vm.selectedCombi.length != 3) {
        func.openToaster('3');
      } else if (vm.selectedCombi[0] == vm.selectedCombi[1] || vm.selectedCombi[0] == vm.selectedCombi[2] || vm.selectedCombi[1] == vm.selectedCombi[2]) {
        func.openToaster('3');
      } else {
        addRecommendRule(); //
      }
    } else {
      addRecommendRule();
    }
  }

  //
  function addRecommendRule() {
    recommendCombineRuleService.recommendCombiRuleAdd(
      {
        company_id: vm.companyId,
        recommend_biz_type: '2', //1-2-3-
        recommend_order_type: vm.recomendRadio //0-7-5-
      },
      function(result) {
        if (vm.recomendRadio == '5') {
          RecommendedCombiQry(); //
        } else {
          func.openToaster('');
        }
      }
    );
  }

  //
  function getRecommendedCombi() {
    recommendCombineRuleService.combiRecommendQry(
      {
        company_id: vm.companyId
      },
      function(result) {
        vm.selectedCombi = [];
        angular.forEach(result.data_list, function(item) {
          var obj1 = item.combi_code + '_' + item.combi_name;
          vm.selectedCombi.push(obj1);
          vm.selectedCombiId.push(item.combi_code);
        });
      }
    );
  }

  //
  function RecommendedCombiQry() {
    recommendCombineRuleService.combiRecommendQry(
      {
        company_id: vm.companyId
      },
      function(result) {
        angular.forEach(result.data_list, function(item) {
          vm.recommendedUserId.push(item.combi_code);
        });
        if (vm.recommendedUserId.length > 0) {
          //
          cancelRecommend();
        } else {
          //
          recommendFunc(0);
        }
      }
    );
  }

  //
  function cancelRecommend() {
    if (vm.recommendedUserId[vm.cancelNum] != '' && vm.cancelNum <= vm.recommendedUserId.length - 1) {
      vm.adviserId = vm.recommendedUserId[vm.cancelNum];
      recommendCombineRuleService.combiRecommend(
        {
          company_id: vm.companyId,
          recommend_flag: '2', //
          combi_code: vm.adviserId
        },
        function(result) {
          vm.cancelNum++;
          cancelRecommend();
        }
      );
    } else {
      vm.cancelNum = 0; //
      vm.recommendedUserId = []; //
      recommendFunc(0);
    }
  }

  //
  function recommendFunc(item) {
    recommendCombineRuleService.combiRecommend(
      {
        company_id: vm.companyId,
        recommend_flag: 1,
        combi_code: vm.selectedCombiId[item]
      },
      function(response) {
        if (item != 2) {
          item++;
          recommendFunc(item);
        } else {
          func.openToaster('');
        }
      },
      function(result) {
        var options = angular.copy(modalOptions);
        options.modalTitle = '';
        options.modalInfo = result.error_info;
        modalService.openModal(options);
      }
    );
  }

  // //
  // function selectAdviser(item) {
  //   var obj1 = item.combi_code + '_' + item.combi_name;
  //   var obj2 = item.combi_code;
  //   if (vm.selectedCombi.indexOf(obj1) == -1 && vm.selectedCombi.length < 3) {
  //     vm.selectedCombi.push(obj1);
  //     vm.selectedCombiId.push(obj2);
  //   }
  // }

  // 
  function upHotCombine(item) {
    var obj1 = item.combi_code + '_' + item.combi_name;
    var obj2 = item.combi_code;
    if (vm.selectedCombi.indexOf(obj1) == -1 && vm.selectedCombi.length < 3) {
      vm.selectedCombi.push(obj1);
      vm.selectedCombiId.push(obj2);
    }
    // console.log('upHotCombine', vm.selectedCombi, vm.selectedCombiId);
  }

  //
  function deleteAdviser(item) {
    if (item == 'adviser0') {
      vm.selectedCombi.splice(0, 1);
      vm.selectedCombiId.splice(0, 1);
    } else if (item == 'adviser1') {
      vm.selectedCombi.splice(1, 1);
      vm.selectedCombiId.splice(1, 1);
    } else {
      vm.selectedCombi.splice(2, 1);
      vm.selectedCombiId.splice(2, 1);
    }
    // console.log('deleteAdviser', vm.selectedCombi, vm.selectedCombiId);
  }
}

module.exports = recommendCombineRuleCtrl;



