 // 

require('locale_zh');

var func = require('func');

productAuthorityManageCtrl.$inject = ['modalService', 'productAuthorityManageService'];

function productAuthorityManageCtrl(modalService, productAuthorityManageService) {
  var vm = this;

  /*  */
  vm.maxSize = 5;
  vm.totalItems = 0;
  vm.currentPage = 1;
  vm.itemPerPage = 10;

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));

  vm.selectTab = 3; // tab 34
  vm.customName = '';
  vm.customMobile = '';
  vm.authorityStatus = '';
  vm.dictsAuthorityStatus = [
    {label: '', value: ''},
    {label: '', value: 1},
    {label: '', value: 0},
  ];
  vm.productId = '';
  vm.productName = '';
  vm.productType = '';
  vm.dictsProductType = [
    {label: '', value: ''},
    {label: '', value: 0},
    {label: '', value: 1},
    {label: '', value: 2},
    {label: '', value: 3},
    {label: '', value: 4},
    {label: '', value: 8},
    {label: '', value: 98},
    {label: '', value: 99}
  ];
  /*  */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };
  vm.startTime = '';
  vm.endTime = '';
  vm.keyword = '';
  vm.dataList = [];
  
  // 
  vm.orderByName = ''; // 1234
  vm.sortOrder = 1; // 12
  vm.seqUser = false; // 
  vm.seqMobile = false; // 
  vm.seqProduct = false; // 
  vm.seqAuthState = false; // 
  vm.seqUpState = false; // 
  vm.seqProductType = false; // 
  vm.seqStartTime = false; // 
  vm.seqEndTime = false; // 

  vm.openStartTimeFunc = openStartTimeFunc; //
  vm.openEndTimeFunc = openEndTimeFunc; //
  vm.selectTabFunc = selectTabFunc;
  vm.sequenceFunc = sequenceFunc;
  vm.searchFunc = searchFunc;
  vm.pageChangeFunc = pageChangeFunc;

  getCustomerContributionQryFunc('init');
  
  // tab
  function selectTabFunc(value) {
    vm.currentPage = 1;
    vm.selectTab = value;
    vm.dataList = [];
    getCustomerContributionQryFunc('init');
  }

  function getCustomerContributionQryFunc(type) {
    if (type === 'init') {
      vm.customName = '';
      vm.customMobile = '';
      vm.authorityStatus = '';
      vm.productId = '';
      vm.productName = '';
      vm.productType = '';
      vm.startTime = '';
      vm.endTime = '';
      vm.keyword = '';
      vm.dataList = [];
      vm.seqUser = false;
      vm.seqMobile = false;
      vm.seqProduct = false;
      vm.seqAuthState = false;
      vm.seqUpState = false;
      vm.seqProductType = false; // 
      vm.seqStartTime = false; // 
      vm.seqEndTime = false; // 
      vm.orderByName = '';
      vm.sortOrder = '';
    }
    productAuthorityManageService.productListQryFunc({
      tenant_id: vm.tenantId,
      interface_flag: vm.selectTab, // 34
      customer_name: vm.customName,
      customer_mobile: vm.customMobile,
      order_type: vm.productType,
      relation_id: vm.productId,
      relation_name: vm.productName,
      auth_flag: vm.authorityStatus,
      // keyword: vm.keyword,
      sort_type: String(vm.orderByName), // 1234678
      sort_mode: String(vm.sortOrder), // 12
      begin_time: vm.startTime && func.formatDate(vm.startTime, 'yyyyMMdd') || '',
      end_time: vm.endTime && func.formatDate(vm.endTime, 'yyyyMMdd') || '',
      page_no: String(vm.currentPage),
      page_count: String(vm.itemPerPage)
    }, function(res) {
      console.log('res', res);
      vm.dataList = res.data_list || [];
      vm.totalItems = res.total_item_num || 0;
    }, function(err) {
      console.log('err', err);
      modalService.openAlert('', err.error_info);
      vm.dataList = [];
      vm.totalItems = 0;
    })
  }

  function sequenceFunc(type) {
    switch (type) {
      case 0:
        vm.seqUser = !vm.seqUser;
        vm.sortOrder = vm.seqUser ? 1 : 2;
        vm.orderByName = 1;
        break;
      case 1:
        vm.seqMobile = !vm.seqMobile;
        vm.sortOrder = vm.seqMobile ? 1 : 2;
        vm.orderByName = 2;
        break;
      case 2:
        vm.seqProduct = !vm.seqProduct;
        vm.sortOrder = vm.seqProduct ? 1 : 2;
        vm.orderByName = 3;
        break;
      case 3:
        vm.seqAuthState = !vm.seqAuthState;
        vm.sortOrder = vm.seqAuthState ? 1 : 2;
        vm.orderByName = 4;
        break;
      case 4:
        vm.seqUpState = !vm.seqUpState;
        vm.sortOrder = vm.seqUpState ? 1 : 2;
        vm.orderByName = 4;
        break;
      case 6:
        vm.seqProductType = !vm.seqProductType;
        vm.sortOrder = vm.seqProductType ? 1 : 2;
        vm.orderByName = 6;
        break;
      case 7:
        vm.seqStartTime = !vm.seqStartTime;
        vm.sortOrder = vm.seqStartTime ? 1 : 2;
        vm.orderByName = 7;
        break;
      case 8:
        vm.seqEndTime = !vm.seqEndTime;
        vm.sortOrder = vm.seqEndTime ? 1 : 2;
        vm.orderByName = 8;
        break;
    }
    vm.currentPage = 1;
    getCustomerContributionQryFunc();
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  };

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function searchFunc() {
    vm.currentPage = 1;
    getCustomerContributionQryFunc();
  }

  function pageChangeFunc() {
    getCustomerContributionQryFunc();
  }
}

module.exports = productAuthorityManageCtrl; 


