 /*  */

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
viewpointPackageCheckModalTempl = require('./viewpointPackageCheckModal.html');

  viewpointPackageCheckModalCtrl.$inject = ['modalService','viewpointPackageCheckModalService'];

function viewpointPackageCheckModalCtrl(modalService,viewpointPackageCheckModalService) {
  var vm = this;

  /**/
  vm.openViewpointPackageCheckModalFunc = openViewpointPackageCheckModalFunc;

  /**/
  function openViewpointPackageCheckModalFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '';
    options.template = viewpointPackageCheckModalTempl;
    options.controller = defaultController;
    options.modalParams = data;
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller*/
  function defaultController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = '';
    ra.cancelBtnText = '';

    ra.operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
    ra.tenantId = JSON.parse(sessionStorage.getItem('tenantId')) * 1;
    ra.remark = '';

    ra.submit = submit;
    ra.cancel = cancel;
    ra.close = close;

    function submit(type) {
      // 
      if (type == 2 && !ra.remark) {
        modalService.openAlert('', '');
        return;
      }
      viewpointPackageCheckModalService.viewpointPkgAudit({
        tenant_id: ra.tenantId,
        audit_ids: ra.modalParams.audit_id,
        operate_content: type, // 12
        operator_no: ra.operatorInfo.operatorNo,
        operator_name: ra.operatorInfo.name,
        viewpoint_tag_instruction: ra.tagInfo,
        remark: ra.remark
      }, function(res) {
        if (res.error_no != 0) {
          modalService.openAlert('', res.error_no.error_info);
        } else {
          modalService.openAlert('', '');
        }
        close();
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('', err.error_info);
      })
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = viewpointPackageCheckModalCtrl;




