/*  controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  addBranchModalTempl = require('./addBranchModal.html'),
  func = require('func');

addBranchModalCtrl.$inject = ['modalService', 'addBranchModalService'];

function addBranchModalCtrl(modalService, addBranchModalService) {
  var vm = this;

  vm.selectedBranchInfo = {}; //

  /**/
  vm.openaddBranchModalFunc = openaddBranchModalFunc;

  /**/
  function openaddBranchModalFunc(info, callback) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '';
    options.template = addBranchModalTempl;
    options.controller = addBranchModalController;
    options.confirmCallback = function() {
      angular.isFunction(callback) && callback();
    };
    vm.selectedBranchInfo = info;
    modalService.openModal(options);
  }

  /*controller*/
  function addBranchModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, submitText, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.submitText = submitText;
    ra.cancelBtnText = cancelBtnText;

    ra.upperBranch = vm.selectedBranchInfo.branchNo + '-' + vm.selectedBranchInfo.branchName; //
    ra.branchName = ''; //
    ra.typeOptions = [{ code: '1', value: '' }, { code: '2', value: '' }, { code: '3', value: '' }, { code: '4', value: '' }]; //
    ra.chargeName = ''; //
    ra.chargeTelephone = ''; //
    ra.branchCode = ''; //
    ra.remark = ''; //

    ra.cancel = cancel;
    ra.close = close;
    ra.submit = submit;

    function submit() {
      if (ra.branchName == '' || ra.branchName == undefined) {
        modalService.openAlert('', '');
        return;
      }
      if (ra.branchType == '' || ra.branchType == undefined) {
        modalService.openAlert('', '');
        return;
      }

      addBranchModalService.branchAdd(
        {
          action_in: 1,
          branchNo: '',
          branchName: ra.branchName,
          branchType: ra.branchType,
          branchCode: ra.branchCode,
          chargeName: ra.chargeName,
          chargeTelephone: ra.chargeTelephone,
          upperBranchNo: vm.selectedBranchInfo.branchNo,
          upperBranchName: vm.selectedBranchInfo.branchName,
          upperBranchStyle: vm.selectedBranchInfo.branchNo + '-' + vm.selectedBranchInfo.branchName,
          menuRights: '',
          remark: ra.remark
        },
        function(result) {
          func.openToaster('');
          close();
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = addBranchModalCtrl;



