/*  controller*/

require('./style.less');
window.ExcellentExport = require('ExcellentExport'); //
var modalOptions = require('jsDir/modal_options'),
signStatisticsDownloadTempl = require('./signStatisticsDownload.html');

signStatisticsDownloadCtrl.$inject = ['modalService', 'signStatisticsDownloadService'];

function signStatisticsDownloadCtrl(modalService, signStatisticsDownloadService) {
  var vm = this;

  /**/
  vm.openDownModalFunc = openDownModalFunc;

  /**/
  function openDownModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '';
    options.template = signStatisticsDownloadTempl;
    options.size = 'md';
    options.controller = downModalController;
    options.modalParams = data; //
    modalService.openModal(options);
  }

  /*controller*/
  function downModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.currentSession = ra.modalParams.current_session; // 01
    ra.cancelBtnText = cancelBtnText;
    ra.content = '...0%';
    ra.signStatisticsData = '';
    ra.downFlag = true;

    ra.cancel = cancel;
    ra.close = close;

    init();

    function init() {
      // 01
      if (ra.currentSession == 0) {
        signStatisticsDownloadService.branchSignStatisticQryFunc(
          {
            tenant_id: ra.modalParams.tenant_id,
            branch_no: ra.modalParams.branch_no,
            service_type: ra.modalParams.serviceType,
            start_time: ra.modalParams.start_time,
            end_time: ra.modalParams.end_time,
            page_no: ra.modalParams.page_no,
            page_size: ra.modalParams.page_size
          },
          function(response) {
            handleDataListFunc(response.data_list);
            ra.downFlag = false;
            ra.content = '...100%';
          },
          function(err) {
            ra.content = '';
          }
        );
      } else if (ra.currentSession == 1) {
        signStatisticsDownloadService.brokerSignStatisticQryFunc(
          {
            tenant_id: ra.modalParams.tenant_id,
            branch_no: ra.modalParams.branch_no,
            service_type: ra.modalParams.serviceType,
            broker_name: ra.modalParams.brokerName,
            start_time: ra.modalParams.start_time,
            end_time: ra.modalParams.end_time,
            page_no: ra.modalParams.page_no,
            page_size: ra.modalParams.page_size
          },
          function(response) {
            handleDataListFunc(response.data_list);
            ra.downFlag = false;
            ra.content = '...100%';
          },
          function(err) {
            ra.content = '';
          }
        );
      }
    }

    function handleDataListFunc(dataList) {
      ra.signStatisticsData = [];
      // 
      if (dataList.length) {
        dataList.forEach(function(item) {
          if (item.sign_statistic && item.sign_statistic.length) {
            item.sign_statistic.forEach(function(per) {
              ra.signStatisticsData.push({
                branchName: item.branch_name,
                brokerName: item.broker_name,
                totalSignNum: per.total_sign_num,
                newSignNum: per.new_sign_num
              })
            })
          } else {
            ra.signStatisticsData.push({
              branchName: item.branch_name,
              brokerName: item.broker_name
            })
          }
        })
      }
    }
  

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = signStatisticsDownloadCtrl;



