/*  */

require('./style.less');

//
var Routing = require('./route');

// servicecontrollerdirective
var ctrl = require('./controller');
var service = require('./service');
var signProtocolViewModalCtrl = require('./modalPopup/signProtocolViewModal/controller');
var downloadProtocolModalCtrl = require('./modalPopup/downloadProtocolModal/controller');
var downloadProgressModalCtrl = require('./modalPopup/downloadProgressModal/controller');
var downloadProgressModalService = require('./modalPopup/downloadProgressModal/service');

//setter
module.exports = angular
  .module('signProtocolManage', [])
  .config(Routing)
  .controller('signProtocolManageCtrl', ctrl)
  .service('signProtocolManageService', service)
  .factory('signProtocolViewModalCtrl', signProtocolViewModalCtrl)
  .factory('downloadProtocolModalCtrl', downloadProtocolModalCtrl)
  .factory('downloadProgressModalCtrl', downloadProgressModalCtrl)
  .service('downloadProgressModalService', downloadProgressModalService).name;



