require('locale_zh');
var func = require('../utils');


orderInfoQryCtrl.$inject = ['$state', 'modalService', 'orderInfoQryService', 'orderInfoDownloadCtrl', 'editOrderInfoModalCtrl'];

function orderInfoQryCtrl($state, modalService, orderInfoQryService, orderInfoDownloadCtrl, editOrderInfoModalCtrl) {
  var vm = this;

  /*  */
  vm.popupStartTimeOpened = false;
  vm.popupEndTimeOpened = false;
  vm.dateOptions = {
    startingDay: 1,
    showWeeks: false
  };

  vm.tenantId = JSON.parse(sessionStorage.getItem('tenantId'));
  vm.startTime = '';
  vm.endTime = '';
  vm.orderStatus = '';
  vm.orderStatusList = [
    { value: '', label: '' },
    { value: 0, label: '' }, 
    { value: 1, label: '' }, 
    { value: 2, label: '' }, 
    { value: 3, label: '' },
    { value: 4, label: '' },
    { value: 5, label: '' }
  ];
  vm.payType = '';
  vm.payTypeList = [
    { value: '', label: '' },
    { value: 0, label: '' }, 
    { value: 1, label: '' }, 
    { value: 2, label: '' } 
  ];
  vm.orderId = '';
  vm.productType = '';
  vm.dictsProductType = [
    {label: '', value: ''},
    {label: '', value: 1},
    {label: '', value: 2},
    {label: '', value: 3},
    {label: '', value: 8},
    {label: '', value: 98},
    {label: '', value: 99}
  ];
  vm.productName = '';
  vm.recommender = '';
  vm.keyword = '';
  vm.orderList = [];

  // 
  vm.currentPage = 1;
  vm.itemPerPage = 10;
  vm.totalItems = 0;

  vm.openStartTimeFunc = openStartTimeFunc; //
  vm.openEndTimeFunc = openEndTimeFunc; //
  vm.pageChangeFunc = pageChangeFunc;
  vm.searchByKeyword = searchByKeyword;
  vm.download = download;
  vm.editOrderInfo = editOrderInfo;
  vm.linkToProtocolPage = linkToProtocolPage;

  getOrderListFunc();

  function getOrderListFunc() {
    var beginDate = vm.startTime ? func.formatDate(vm.startTime, 'yyyyMMdd') : ''
    var endDate = vm.endTime ? func.formatDate(vm.endTime, 'yyyyMMdd') : ''
    if ((beginDate * 1) > (endDate * 1)) {
      modalService.openAlert('', '');
      return;
    }

    orderInfoQryService.orderManageFunc({
      tenant_id: vm.tenantId,
      platform: 'manage',
      keyword: vm.keyword,
      begin_date: beginDate,
      end_date: vm.endTime ? String(Number(endDate) + 1) : '', // 000000
      order_status: vm.orderStatus,
      third_type: vm.payType,
      recommend_name: vm.recommender,
      transaction_id: vm.orderId,
      product_type: vm.productType,
      product_name: vm.productName,
      page_no: vm.currentPage,
      page_size: vm.itemPerPage
    }, function(res) {
      if (res.error_no != 0) {
        modalService.openAlert('', res.error_info);
      }
      vm.orderList = res.data_list;
      vm.totalItems = res.total_item_num;
    }, function(err) {
      modalService.openAlert('', err.error_info);
      vm.orderList = [];
      vm.totalItems = 0;
    });
  }

  function download() {
    var beginDate = vm.startTime ? func.formatDate(vm.startTime, 'yyyyMMdd') : ''
    var endDate = vm.endTime ? func.formatDate(vm.endTime, 'yyyyMMdd') : ''
    orderInfoDownloadCtrl.openDownModalFunc({
      tenant_id: vm.tenantId,
      keyword: vm.keyword,
      begin_date: beginDate,
      end_date: vm.endTime ? String(Number(endDate) + 1) : '', // 000000
      order_status: vm.orderStatus,
      third_type: vm.payType,
      recommend_name: vm.recommender,
      page_no: 1,
      page_size: vm.totalItems
    })
  }

  function editOrderInfo(data) {
    editOrderInfoModalCtrl.openEditOrderInfoModalFunc(data, function() {
      getOrderListFunc();
    });
  }

  function openStartTimeFunc() {
    vm.popupStartTimeOpened = true;
  }

  function openEndTimeFunc() {
    vm.popupEndTimeOpened = true;
  };

  function pageChangeFunc() {
    getOrderListFunc();
  }

  function searchByKeyword() {
    getOrderListFunc();
  }

  function linkToProtocolPage(data) {
    console.log('data', data);
    $state.go('home.signProtocolManage', {
      data: JSON.stringify(data)
    });
  }
}

module.exports = orderInfoQryCtrl;



