/*  module */

require('./style.less');

//
var Routing = require('./route');

// servicecontrollerdirective
var ctrl = require('./controller');
var service = require('./service');
var editCourseModalCtrl = require('./modalPopup/editCourseModal/controller');
var editCourseModalService = require('./modalPopup/editCourseModal/service');
var dayCourseModalCtrl = require('./modalPopup/dayCourseModal/controller');
var dayCourseModalService = require('./modalPopup/dayCourseModal/service');

//setter
module.exports = angular
  .module('liveManage', [])
  .config(Routing)
  .service('liveManageService', service)
  .controller('liveManageCtrl', ctrl)
  .service('editCourseModalService', editCourseModalService)
  .factory('editCourseModalCtrl', editCourseModalCtrl)
  .service('dayCourseModalCtrl', dayCourseModalCtrl)
  .factory('dayCourseModalService', dayCourseModalService).name;



