/*  controller*/

require('./style.less');
window.ExcellentExport = require('ExcellentExport'); //
var modalOptions = require('jsDir/modal_options'),
customerContributionQryDownloadTempl = require('./customerContributionQryDownload.html');

customerContributionQryDownloadCtrl.$inject = ['modalService', 'customerContributionQryDownloadService'];

function customerContributionQryDownloadCtrl(modalService, customerContributionQryDownloadService) {
  var vm = this;

  /**/
  vm.openDownModalFunc = openDownModalFunc;

  /**/
  function openDownModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '';
    options.template = customerContributionQryDownloadTempl;
    options.size = 'md';
    options.controller = downModalController;
    options.modalParams = data; //
    modalService.openModal(options);
  }

  /*controller*/
  function downModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.cancelBtnText = cancelBtnText;
    ra.content = '...0%';
    ra.customerData = '';
    ra.downFlag = true;

    ra.cancel = cancel;
    ra.close = close;

    init();

    function init() {
      customerContributionQryDownloadService.customerContributionQryFunc(
        {
          tenant_id: ra.modalParams.tenant_id,
          keyword: ra.modalParams.keyword,
          order_by_name: ra.modalParams.order_by_name,
          sort_order: ra.modalParams.sort_order,
          page_no: ra.modalParams.page_no,
          page_size: ra.modalParams.page_size
        },
        function(response) {
          ra.customerData = response.data_list;
          ra.downFlag = false;
          ra.content = '...100%';
        },
        function(err) {
          ra.content = '';
        }
      );
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = customerContributionQryDownloadCtrl;



