/*  module */

require('./style.less');

//
var Routing = require('./route');

// servicecontrollerdirective
var ctrl = require('./controller');
var service = require('./service');
var investorDownloadModalCtrl = require('./modalPopup/investorDownloadModal/controller');
var investorDownloadModalService = require('./modalPopup/investorDownloadModal/service');
var updatePwdModalCtrl = require('./modalPopup/updatePwdModal/controller');
var updatePwdModalService = require('./modalPopup/updatePwdModal/service');

//setter
module.exports = angular.module('investManagement.investManage', [])
  .config(Routing)
  .controller('investManageCtrl', ctrl)
  .service('investManageService', service)
  .factory('investorDownloadModalCtrl', investorDownloadModalCtrl)
  .service('investorDownloadModalService', investorDownloadModalService)
  .factory('updatePwdModalCtrl', updatePwdModalCtrl)
  .service('updatePwdModalService', updatePwdModalService)
  .name;



