/**/

require('locale_zh');
require('script-loader!ng-table-excel-export/ng-table-excel-export');
require.ensure([], function (require) {
  XLSX = require('xlsx');
}, 'excel');
 var func = require('func');

batchAddNewInvestCtrl.$inject = ['$scope', 'batchAddNewInvestService', '$cookies'];
function batchAddNewInvestCtrl($scope, batchAddNewInvestService, $cookies) {
  var vm = this;
  var wb;//
  var rABS = false; //
  vm.name = '';
  vm.myFile = '';
  vm.excelList = '';
  vm.download = download;//excel
  vm.saveInvestInfo = saveInvestInfo; //
  vm.readAsText = readAsText; //
  var certificatedType = {
    '': '1',
    '': '2',
    '': '3',
    '': '4',
    '': '5',
    '': '6',
    '': '7',
    '': '8'
  };
  var adviserType = {
    '': '0',
    '': '1',
    '': '2',
    '': '3',
    '': '4',
    '': '5',
    '': '6',
    '': '7'
  };

  //excel
  function download() {
    window.open('assets/template/batchAddInvestorTemplate.xlsx');
  }

  //
  function saveInvestInfo() {
    //
    angular.forEach(vm.list, function (item) {
      item.adviserType = adviserType[item.adviserType];
      item.credentialsType = certificatedType[item.credentialsType];
    });
    batchAddNewInvestService.saveFunc({
      adviserList: vm.list
    }, function (response) {
      func.openToaster('');
    }, function () {
      func.openToaster('');
    })
  }

  //
  function readAsText(event) {
    var file = event.currentTarget.files[0];
    vm.myFile = event.currentTarget.value;
    if (!file) {
      return;
    }
    var reader = new FileReader();
    reader.onload = function (e) {
      var data = e.target.result;
      if (rABS) {
        wb = XLSX.read(btoa(fixdata(data)), {//
          type: 'base64'
        });
      } else {
        wb = XLSX.read(data, {
          type: 'binary'
        });
      }
      //wb.SheetNames[0]SheetsSheet
      //wb.Sheets[Sheet]Sheet
      vm.adviserInfoList = JSON.stringify(XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]));
      vm.list = JSON.parse(vm.adviserInfoList);
      if (vm.list != undefined && vm.list.length != 0) {
        for (var k = 0; k < vm.list.length; k++) {
          var bbb = vm.list[k];
          for (var j in bbb) {
            var value = bbb[j];
            if (j == "") {
              delete bbb[j];
              j = "applicantName";
              bbb["aliasName"] = value; // 
              bbb[j] = value;
              vm.excelList = bbb;
            }
            if (j == "") {
              delete bbb[j];
              j = "idCardNo";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "businessDept";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "adviserType";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "email";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "aliasName";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "mobile";
              bbb[j] = value;
            }
            if (j == "ID") {
              delete bbb[j];
              j = "tenantId";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "branchNo";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "accountHolder";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "backgroundIntro";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "bankAccount";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "cityName";
            }
            if (j == "") {
              delete bbb[j];
              j = "companyName";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "credentialsNo";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "credentialsType";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "employeeNo";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "investmentStrategy";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "investmentStrategySummary";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "investmentStyle";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "openingBank";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "organization";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "provinceName";
              bbb[j] = value;
            }
            if (j == "qq") {
              delete bbb[j];
              j = "qqNo";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "referralCode";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "weiboNo";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "weixinNo";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "workYear";
              bbb[j] = value;
            }
            if (j == "") {
              delete bbb[j];
              j = "authPassword";
              bbb[j] = func.MD5(value.trim()).toUpperCase();
            }
            if (j == "") {
              delete bbb[j];
              j = "weixinOwner";
              if (value == '') {
                bbb[j] = 0;
              } else {
                bbb[j] = 1;
              }
            }
            if (j == "") {
              delete bbb[j];
              j = "sex";
              if (value == '') { // 0: 1:
                bbb[j] = 0;
              } else {
                bbb[j] = 1;
              }
            }
            if (j == "applicantName" || j == "idCardNo" || j == "businessDept" || j == "investmentStyle" || j == "email" ||
              j == "aliasName" || j == "mobile" || j == "tenantId" || j == "branchNo" || j == "accountHolder" || j == "backgroundIntro" ||
              j == "bankAccount" || j == "cityName" || j == "companyName" || j == "credentialsNo" || j == "credentialsType" ||
              j == "employeeNo" || j == "investmentStrategy" || j == "investmentStrategySummary" || j == "investmentStyle" ||
              j == "openingBank" || j == "organization" || j == "provinceName" || j == "qqNo" || j == "referralCode" || j == "weiboNo" ||
              j == "weixinNo" || j == "workYear" || j == "authPassword" || j == "weixinOwner" || j == "sex" || j == "adviserType" ||
              j == "registerFlag") {
            } else {
              func.openToaster('');
              return;
            }
          }
        }
      } else {
        func.openToaster('');
        // 
        event.target.value = '';
        return;
      }
      // 
      event.target.value = '';
      $scope.$apply();
    };
    if (rABS) {
      reader.readAsArrayBuffer(file);
    } else {
      reader.readAsBinaryString(file);
    }

  }

  //BinaryString
  function fixdata(data) {
    var o = "",
      l = 0,
      w = 10240;
    for (; l < data.byteLength / w; ++l) o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)));
    o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
    return o;
  }
}

module.exports = batchAddNewInvestCtrl;



