require('./style.less');

var modalOptions = require('jsDir/modal_options'),
combineDownModalTempl = require('./combineDownModal.html');

  combineDownModalCtrl.$inject = ['modalService', 'combineDownModalService'];

function combineDownModalCtrl(modalService, combineDownModalService) {
  var vm = this;

  /**/
  vm.openCombineDownModalFunc = openCombineDownModalFunc;


  /**/
  function openCombineDownModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '';
    options.template = combineDownModalTempl;
    options.size = 'md';
    options.controller = combineOnlineModalController;
    options.modalParams = data;
    options.confirmCallback = function (result) {
      angular.isFunction(callback) && callback(result);
    };
    modalService.openModal(options);
  }

  /*controller*/
  function combineOnlineModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, isShowCancelBtn, confirmBtnText, cancelBtnText, ensureBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;

    // 
    ra.tenantId = JSON.parse(sessionStorage.getItem("tenantId"));

    // 
    ra.riskLevel = ['', '', '', '', '', '', ''];
    
    /*  */
    ra.maxSize = 10;
    ra.totalItems = 0;
    ra.currentPage = 1;
    ra.itemPerPage = 5;

    ra.combineStatus = '';
    ra.combineStatusList = [
      { value: '', label: '' }, 
      { value: 0, label: '' }, 
      { value: 1, label: '' }, 
      { value: 2, label: '' }, 
      { value: 3, label: '' },
      { value: 4, label: '' }
    ];
    ra.combineName = '';     // 
    ra.combineCreator = '';     // 
    ra.combineList = [];    // 

    ra.setCombineUnderlineFunc = setCombineUnderlineFunc;
    ra.keywordSearchFunc = keywordSearchFunc;
    ra.pageChangeFunc = pageChangeFunc;
    ra.cancel = cancel;
    ra.close = close;

    getCombineListFunc();

    function getCombineListFunc() {
      combineDownModalService.getCombineList({
        // type: 2, // 12
        combine_status: ra.combineStatus, // 0-1-2-3-4-
        tenant_id: ra.tenantId,
        combi_name: ra.combineName,
        broker_name: ra.combineCreator,
        page_no: ra.currentPage,
        page_size: ra.itemPerPage
      }, function(res) {
        if (res.data_list && res.data_list.length) {
          ra.combineList = res.data_list;
        } else {
          ra.combineList = [];
        }
        ra.totalItems = res.total_num || 0;
      }, function(err) {
        console.log('err', err);
        ra.combineList = [];
        ra.totalItems = 0;
      })
    }

    function setCombineUnderlineFunc(id) {
      combineDownModalService.setCombineUnderline({
        combine_info_ids: id
      }, function(res) {
        getCombineListFunc();
        modalService.openAlert('', res.error_info);
      }, function(err) {
        console.log('err', err);
        modalService.openAlert('', err.error_info);
      })
    }

    function keywordSearchFunc() {
      ra.currentPage = 1;
      getCombineListFunc();
    }

    function pageChangeFunc() {
      getCombineListFunc();
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = combineDownModalCtrl;


