/*  module */

require('locale_zh');
var modalOptions = require('jsDir/modal_options'),
redirectConfig = require('./redirectConfig');
menuIcon = require('../directives/menuView/menuIconConfig');

homeCtrl.$inject = ['$scope', '$rootScope', '$state', '$cookies', 'homeService', '$sce', 'modalService', 'userInfoModalCtrl', 'userInfoModalService'];

function homeCtrl($scope, $rootScope, $state, $cookies, homeService, $sce, modalService, userInfoModalCtrl, userInfoModalService) {
  var vm = this;

  vm.navData = []; //
  vm.navDataShow = []; //
  vm.topMenuData = []; //
  vm.menuViewData = []; //

  vm.userInfoData = {}; //
  vm.userName = ''; //
  vm.operatorNo = ''; //
  vm.roleType = ''; //  1
  vm.telephone = ''; //
  vm.branchName = ''; //
  vm.remark = ''; //
  vm.companyId = '';

  vm.unfoldSide = false; //
  vm.selectedBtn = 0; //

  //
  vm.modulesHeight = document.body.clientHeight - 64 - 10; // 10px
  $('#wrapperHeight').css('height', vm.modulesHeight + 'px');

  //
  vm.changeTopMenu = changeTopMenu;
  vm.shrinkNav = shrinkNav;
  vm.exit = exit;
  vm.openUserInfoModal = openUserInfoModal;
  vm.goHome = goHome;

  getAllBranch();

  //
  function getAllBranch() {
    homeService.allBranchQry(
      {
        deepth: -1
      },
      function(response2) {
        //
        sessionStorage.setItem('branchQryData', JSON.stringify(response2.data));
        userInfoQry();
        getAllbranchList();
      }
    );
  }

  //
  function getAllbranchList() {
    homeService.allBranchQry({}, function(response4) {
      sessionStorage.setItem('allBranchListData', JSON.stringify(response4.data));
      // userInfoQry();
    });
  }

  //peakHead
  function getBranchInfo() {
    var branchInfoList = JSON.parse(sessionStorage.getItem('branchQryData')) || [];
    var branchInfo = branchInfoList[0] || '';
    var tmpArr = [];
    if (branchInfo && branchInfo.branchCode.indexOf('_') > -1) {
      tmpArr = branchInfo.branchCode.split('_');
    } else if (branchInfo && branchInfo.leafFlag == '0' && !(branchInfo.branchCode.indexOf('_') > -1)) {
      var breakFlag = true;
      /*
       * 
       * 
       * */
      angular.forEach(branchInfo['children'], function(data) {
        if (breakFlag && data.branchCode.indexOf('_') > -1) {
          tmpArr = data.branchCode.split('_');
          breakFlag = false;
        }
      });
    }
    var packHead = {
      company_id: tmpArr[0] ? tmpArr[0] : '8888',
      sendercomp_id: tmpArr[0] ? tmpArr[0] : '8888',
      businsys_no: 1000,
      branch_no: tmpArr[1] ? tmpArr[1] : '8888'
    };
    sessionStorage.setItem('packHead', JSON.stringify(packHead));
  }

  //branchNotenantId
  function formatBranchNoData() {
    var branchNo = ''; //branchNotenantId()
    var result = JSON.parse(sessionStorage.getItem('branchQryData'));
    var branchCode = getBranchCode(result[0]);
    var codeArray = branchCode.split(',');
    var tenant_id = codeArray[0].split('_')[0];
    for (var j = 0; j < codeArray.length; j++) {
      var array = codeArray[j].split('_');
      if (array[0] == tenant_id && !branchNo.match(array[0])) {
        branchNo += array[1] + ',';
      }
    }
    branchNo = branchNo.substr(0, branchNo.length - 1);
    sessionStorage.setItem('branchNoData', JSON.stringify(branchNo));
    sessionStorage.setItem('tenantId', JSON.stringify(tenant_id));
  }

  function getBranchCode(root) {
    if (root && root.leafFlag) {
      return checkBranchCode(root.branchCode);
    }
    var str = '';
    for (var i = 0; i < root.children.length; i++) {
      var code = getBranchCode(root.children[i]);
      if (code.trim() != '') {
        str += ',' + code;
      }
    }
    if (root && checkBranchCode(root.branchCode)) {
      return root.branchCode + str;
    }
    return str.substr(1);
  }

  function checkBranchCode(branchCode) {
    var m = '^[0-9]+_[0-9]+$';
    if (branchCode.match(m)) {
      return branchCode;
    }
    return '';
  }

  //
  function getModulesList() {
    homeService.menuQry(
      {
        deepth: -1,
        ancestorFlag: 1,
        branchNo: 0
      },
      function(result) {
        var dataList = result.data;
        // 
        dataList[0].children.forEach(function(item) {
          if (item.menuId == 17) {
            item.children = handleMenuOrderFunc(item.children);
          }
        })
        vm.navData = dataList[0].children
        getMeunIdListFunc(vm.navData);
        // roleType=1
        angular.forEach(vm.navData, function(item) {
          if (vm.roleType == '1') {
            item.menuId == 2 && vm.navDataShow.push(item);
          } else {
            item.menuId != 2 && vm.navDataShow.push(item);
          }  
        })
        vm.menuViewData = vm.navDataShow[0];
        //
        angular.forEach(vm.navDataShow, function(item) {
          var data = {};
          data.name = item.menuCaption;
          data.icon = menuIcon[item.menuId];
          data.id = item.menuId;
          vm.topMenuData.push(data);
        });
        //
        sessionStorage.setItem('menuTree', JSON.stringify(dataList));

        //
        if (sessionStorage.getItem('currentUrl')) {
          var redirectMenu = redirectConfig[JSON.parse(sessionStorage.getItem('currentUrl')).url];
          angular.forEach(vm.navDataShow, function(item, index) {
            if (item.menuCaption == redirectMenu[0]) {
              changeTopMenu(index);
            }
          });
          $rootScope.$emit('redirect', redirectMenu[1]);
        }
      }
    );
  }

  /**
   * 
   *
   * @param   {[type]}  menuList  [menuList description]
   *
   * @return  {[type]}            [return description]
   */
  function handleMenuOrderFunc(menuList) {
    var result = [];
    // menuIdBanner1920222139234525261824445253
    var orderList = [19, 20, 22, 21, 39, 23, 45, 25, 26, 18, 24, 44, 52, 53];
    // // menuIdBanner1920222139234525261824445253
    // var orderList = [19, 20, 22, 21, 39, 23, 45, 25, 26, 18, 24, 44, 52, 53];
    orderList.forEach(function(item) {
      menuList.forEach(function(per) {
        if (per.menuId == item) {
          result.push(per);
        }
      })
    });
    return result;
  }

  /**
   * menuId
   * @param childList
   */
  function getMeunIdListFunc(childList) {
    var menuIdList = [];
    childList.length && childList.forEach(function(item) {
      // 
      menuIdList.push(item.menuId);
      // 
      if (item.children && item.children.length) {
        item.children.forEach(function(child) {
          menuIdList.push((child.menuId));
          // 
          if (child.children && child.children.length) {
            child.children.forEach(function(pre) {
              menuIdList.push(pre.menuId);
            });
          }
        });
      }
    });
    //
    sessionStorage.setItem('menuIdList', JSON.stringify(menuIdList));
  }

  //
  function userInfoQry() {
    homeService.operatorSelfQry({}, function(response5) {
      vm.roleType = response5.data.roleType;
      //
      sessionStorage.setItem('operatorInfo', JSON.stringify(response5.data));
      // 
      getModulesList();
      //
      getBranchInfo();
      //branchNo
      formatBranchNoData();
      //
      vm.userName = response5.data.name;
      vm.operatorNo = response5.data.operatorNo;
      vm.telephone = response5.data.telephone;
      vm.branchName = response5.data.branchNo + '-' + response5.data.branchName;
      vm.remark = response5.data.remark;
      vm.userInfoHtml = $sce.trustAsHtml(
        '<div class="popover-text"><p class="unit"> </p><span class="content">' +
          vm.operatorNo +
          '</span></div>' +
          '<div class="popover-text"><p class="unit"> </p><span class="content">' +
          vm.userName +
          '</span></div>' +
          '<div class="popover-text"><p class="unit"> </p><span class="content">' +
          vm.telephone +
          '</span></div>' +
          '<div class="popover-text"><p class="unit"> </p><span class="content">' +
          vm.branchName +
          '</span></div>'
      );
    });
  }

  //
  function updateUserInfoQry() {
    homeService.operatorSelfQry({}, function(response5) {
      vm.roleType = response5.data.roleType;
      //
      sessionStorage.setItem('operatorInfo', JSON.stringify(response5.data));
      //
      vm.userName = response5.data.name;
      vm.operatorNo = response5.data.operatorNo;
      vm.telephone = response5.data.telephone;
      vm.branchName = response5.data.branchNo + '-' + response5.data.branchName;
      vm.remark = response5.data.remark;
      vm.userInfoHtml = $sce.trustAsHtml(
        '<div class="popover-text"><p class="unit"> </p><span class="content">' +
          vm.operatorNo +
          '</span></div>' +
          '<div class="popover-text"><p class="unit"> </p><span class="content">' +
          vm.userName +
          '</span></div>' +
          '<div class="popover-text"><p class="unit"> </p><span class="content">' +
          vm.telephone +
          '</span></div>' +
          '<div class="popover-text"><p class="unit"> </p><span class="content">' +
          vm.branchName +
          '</span></div>'
      );
    });
  }

  //
  function openUserInfoModal() {
    vm.userInfoData.userName = vm.userName;
    vm.userInfoData.telephone = vm.telephone;
    vm.userInfoData.remark = vm.remark;
    userInfoModalCtrl.openUserInfoModalFunc(vm.userInfoData, function() {
      updateUserInfoQry();
    });
  }

  //
  function changeTopMenu(id) {
    vm.menuViewData = vm.navDataShow[id];
    vm.selectedBtn = id;
  }

  //
  function shrinkNav() {
    vm.unfoldSide = !vm.unfoldSide;
  }

  //
  function exit() {
    sessionStorage.clear();
    $state.go('login');
  }

  //
  function goHome() {
    $state.go('home');
  }

}

module.exports = homeCtrl;



