require('./style.less');

var rightViewTempl = require('./rightView.html');
// 
var jiansyImg = require('img/jiansy.png');
var jiansyxImg = require('img/jiansyx.png');
var jianyImg = require('img/jiany.png');
var jianyxImg = require('img/jianyx.png');
var jiasyImg = require('img/jiasy.png');
var jiasyxImg = require('img/jiasyx.png');
var jiayImg = require('img/jiay.png');
var jiayxImg = require('img/jiayx.png');
var syImg = require('img/sy.png');
