rightTree.$inject = [];

function rightTree() {
  return {
    restrict: 'E',
    scope: {
      id: '@', //id
      pid: '@', //id
      showLevel: '@', //,33
      textField: '@', //idname
      ownResultType: '@', //
      initShow: '@', //
      onlyShowCaption: '=', //
      branchSearch: '=', //nameid
      isRightTree: '=', //rightViewicon
      ownResultFlag: '=', //
      treeData: '=', //
      ownResult: '=', //
      addResult: '=', //
      delResult: '=', //
      itemClicked: '&', //
      changeRight: '&', //
      children: '@', //
      isChange: '=' //
    },
    template: rightViewTempl,
    link: function($scope) {
      var id = $scope.id;
      var copyPid = '$pid'; //idpid$pid
      var pid = $scope.pid ? $scope.pid : copyPid; //pidpid$pid

      var textField = $scope.textField;
      var fidldArry = textField.split(',');
      var showLevel = parseInt($scope.showLevel);
      var ownResultType = $scope.ownResultType;

      var children = $scope.children || 'children';
      var ownResult = $scope.ownResult;
      var initShow = $scope.initShow; //true,false
      var onlyShowCaption = $scope.onlyShowCaption; //

      var ownResultCopy = []; //

      var nodeMap = {}; //ID
      var nodeList = []; //
      var rootList = []; //

      //treeDataownResult
      var treeOverFlag = false; //treeDatatrueownResult
      var ownFlag = false; //ownResult

      var itemTreeList = []; //

      $scope.showField = showField;
      $scope.getItemIcon = getItemIcon;
      $scope.getRightIcon = getRightIcon;
      $scope.isLeaf = isLeaf;
      $scope.changeItemIcon = changeItemIcon;
      $scope.getItemCallback = getItemCallback;
      $scope.warpCallback = warpCallback;
      $scope.changeDisplayRight = changeDisplayRight;

      //
      $scope.$watch('treeData', function(newValue) {
        if (angular.isUndefined(newValue) || newValue.length <= 0) {
          return;
        }
        var data = $scope.treeData;
        nodeMap = {};
        nodeList = [];
        rootList = [];
        if (angular.isArray(data)) {
          //
          for (var i = 0; i < data.length; i++) {
            rootList.push(data[i]);
            scanTree(data[i], 1);
          }
        } else {
          rootList.push(data);
          scanTree(data, 1);
        }
        treeOverFlag = true; //treeDatatrueownResultownResult
        if (ownFlag) {
          //ownResultownResultDeal
          ownResultDeal($scope.ownResult);
        }
      });

      //addResult
      $scope.$watch('addResult', function(newValue, oldValue) {
        if (angular.isUndefined(newValue)) {
          return;
        }
        //
        ownResultCopy = ownResultCopy.concat(oldValue);
      });

      //delResult
      $scope.$watch('delResult', function(newValue, oldValue) {
        if (angular.isUndefined(newValue)) {
          return;
        }
        //
        var len = oldValue.length;
        for (var i = 0; i < len; i++) {
          var index = ownResultCopy.indexOf(oldValue[i]);
          if (index >= 0) {
            ownResultCopy.splice(index, 1);
          }
        }
      });

      //ownResult
      $scope.$watch(
        'ownResult',
        function(newValue) {
          if (angular.isUndefined(newValue) || newValue == 'change') {
            return;
          }
          if (treeOverFlag) {
            //treeDataownResultownResult
            ownResultDeal($scope.ownResult);
          } else {
            ownFlag = true; //ownResulttruetreeDataownResult
          }
        },
        true
      ); //

      //onlyShowCaption
      $scope.$watch('onlyShowCaption', function(newValue) {
        onlyShowCaption = newValue;
      });

      //branchSearchnameid
      $scope.$watch('branchSearch', function(newValue) {
        if (newValue != undefined) {
          itemTreeList = []; //
          findParentByNameOrId(newValue);
        }
      });

      //
      function findParentByNameOrId(newValue) {
        angular.forEach(nodeList, function(item) {
          if (item.menuId == newValue || item.menuCaption == newValue || item.branchNo == newValue) {
            //
            if (itemTreeList.indexOf(newValue) == -1) {
              itemTreeList.push(newValue);
            }
            if (item.$pid) {
              //
              itemTreeList.push(item.$pid);
              findParentByNameOrId(item.$pid);
            } else {
              findSearchItemByList(itemTreeList.sort(sortNumber));
            }
          }
        });
      }

      //
      function findSearchItemByList(itemTreeList) {
        for (var i = 0; i < itemTreeList.length; i++) {
          angular.forEach(nodeList, function(item) {
            if (itemTreeList[i] == item.menuId || itemTreeList[i] == item.branchNo) {
              getItemCallback('itemClicked', item);
            }
          });
        }
      }

      //
      function scanTree(node, lvl) {
        nodeMap[node[id]] = node;
        nodeList.push(node);
        node['$level'] = lvl;
        if (showLevel && lvl <= showLevel) {
          //
          node.$isExpend = true; //icon
        } else {
          node.$isExpend = false; //icon
        }
        var children = node['children'];
        if (children && children.length) {
          //
          for (var i = 0; i < children.length; i++) {
            children[i][pid] = node[id]; //pid = id
            scanTree(children[i], lvl + 1); //
          }
        }
      }

      //ownResult
      function scanSelectTree(node) {
        var children = node['children'];
        if (children && children.length) {
          //
          for (var i = 0; i < children.length; i++) {
            children[i][pid] = node[id];
            scanTree(children[i], lvl + 1); //
          }
        }
      }

      //
      function ownResultDeal(newValue) {
        loopTree(function(node) {
          //
          node.$isIcon = 2;
          node.$preIcon = undefined;
        });
        if (ownResultType === 'array') {
          //ownResultType
          ownResultCopy = angular.copy(newValue);
          changeArryDisplayRight(newValue);
        } else {
          ownResultCopy = []; //
          scanSelectTree(newValue);
          changeDisplayRight(newValue);
        }
      }

      //
      function changeArryDisplayRight(ownResult) {
        // var result = $scope.treeData;
        angular.forEach(ownResult, function(targetId) {
          var node = nodeMap[targetId];
          if (!node) {
            //console.error(":" + targetId);
            return;
          }
          if (node.$pid == null || node.$pid == undefined) {
          }
          firstGruntRgihtSelf(node, 1); //
          firstGruntRightParent(node, function(parent) {
            //
            var index = ownResultCopy.indexOf(parent);
            if (index < 0) {
              ownResultCopy.push(parent[id]);
            }
          });
          firstGruntRightChildren(node, function(child) {
            //
            var index = ownResultCopy.indexOf(child);
            if (index < 0) {
              ownResultCopy.push(child[id]);
            }
          });
        });
      }

      //
      function getItemIcon(item, first, middle, last) {
        var isLeaf = $scope.isLeaf(item);
        var iconStr = '';
        if (!isLeaf) {
          //
          if (item.$level === 1) {
            //
            iconStr = syImg;
          } else {
            //
            if (first && last) {
              //
              iconStr = syImg;
            } else if (last) {
              //
              iconStr = syImg;
            } else if (first) {
              //
              iconStr = syxImg;
            } else {
              //
              iconStr = syxImg;
            }
          }
        } else {
          //
          if (item.$level === 1) {
            //
            if (item.$isExpend) {
              //,
              if (first && last) {
                //
                iconStr = jianyImg;
              } else if (last) {
                //
                iconStr = jiansyImg;
              } else if (first) {
                //
                iconStr = jianyxImg;
              } else {
                //
                iconStr = jiansyxImg;
              }
            } else {
              //
              if (first && last) {
                //
                iconStr = jiayImg;
              } else if (last) {
                //
                iconStr = jiasyImg;
              } else if (first) {
                //
                iconStr = jiayxImg;
              } else {
                //
                iconStr = jiasyxImg;
              }
            }
          } else {
            //
            if (item.$isExpend) {
              //,
              if (first && last) {
                //
                iconStr = jiansyImg;
              } else if (last) {
                //
                iconStr = jiansyImg;
              } else if (first) {
                //
                iconStr = jianyxImg;
              } else {
                //
                iconStr = jiansyxImg;
              }
            } else {
              //
              if (first && last) {
                //
                iconStr = jiasyImg;
              } else if (last) {
                //
                iconStr = jiasyImg;
              } else if (first) {
                //
                iconStr = jiayxImg;
              } else {
                //
                iconStr = jiasyxImg;
              }
            }
          }
        }
        return iconStr;
      }

      //
      function changeItemIcon(callback, item) {
        changeSelfIcon(item); //
        changeParentIcon(item); //
        changeChildrenIcon(item); //
        var root = angular.copy($scope.treeData[0]); //
        root[children] = null;
        ($scope[callback] || angular.noop)({
          //callback
          addResult: $scope.addResult,
          delResult: $scope.delResult,
          item: item,
          root: root
        });
      }

      //
      function changeSelfIcon(item) {
        switch (item.$isIcon) {
          case undefined:
            item.$isIcon = 2; //
            break;
          case 1:
            item.$isIcon = 2; //
            break;
          case 2:
            item.$isIcon = 1; //
            break;
          case 3:
            item.$isIcon = 1; //
            break;
        }
        updateResult(item);
      }

      //
      function changeParentIcon(node) {
        firstGruntRightParent(node, function(parentNode) {
          updateResult(parentNode);
        });
      }

      //
      function changeChildrenIcon(item) {
        firstGruntRightChildren(item, function(child) {
          updateResult(child);
        });
      }

      //addResultdelResult
      function updateResult(item) {
        var index = ownResultCopy.indexOf(item[id]);
        var delIndex = $scope.delResult.indexOf(item[id]);
        var addIndex = $scope.addResult.indexOf(item[id]);
        if (index >= 0) {
          //
          if (item.$isIcon === 1 || item.$isIcon === 3) {
            //
            if (delIndex >= 0) {
              $scope.delResult.splice(delIndex, 1); //
            }
          } else if (item.$isIcon === 2) {
            if (delIndex < 0) {
              $scope.delResult.push(item[id]); //
            }
          } else {
            return;
          }
        } else {
          //
          if (item.$isIcon === 1) {
            //icon
            if (addIndex < 0) {
              $scope.addResult.push(item[id]);
            }
          } else if (item.$isIcon === 2) {
            //icon
            if (addIndex >= 0) {
              $scope.addResult.splice(addIndex, 1);
            }
          } else {
            return;
          }
        }
      }

      //
      function getItemCallback(callback, item) {
        if (!$scope[callback]) {
          return;
        }
        if (angular.isDefined($scope.isChange)) {
          //isChange
          $scope.isChange = item[id];
          if (!(angular.element(document.querySelector('#wenjianjia')) && item.children == null)) {
            item.$isExpend = true;
          }
        }
        ($scope[callback] || angular.noop)({
          //itemClicked
          item: item
        });
      }

      //
      function warpCallback(item, $event) {
        if (!(item.children == null)) {
          itemExpended(item, $event);
        }
        return;
      }

      //
      function itemExpended(item, $event) {
        item.$isExpend = !item.$isExpend;
        $event.stopPropagation(); //
      }

      //
      function changeDisplayRight(ownResult) {
        loopTree(function(node) {
          //
          node.$isIcon = 2;
          node.$isExpend = false;
          node.$preIcon = undefined;
        });
        firstGruntRight(ownResult, $scope.treeData); //
      }

      //ownResultidownResultCopyownResultCopy
      function firstGruntRight(targetResult, result) {
        if (angular.isArray(targetResult)) {
          var len = targetResult.length;
          for (var i = 0; i < len; i++) {
            var index = ownResultCopy.indexOf(targetResult[i]); //
            if (index < 0) {
              ownResultCopy.push(targetResult[i][id]);
            }
            if (isLeaf(targetResult[i])) {
              //
              firstGruntRight(targetResult[i][children], result);
            } else {
              firstGruntRight(targetResult[i], result);
            }
          }
        } else {
          var index = ownResultCopy.indexOf(targetResult);
          if (index < 0) {
            ownResultCopy.push(targetResult[id]);
          }
          var node = nodeMap[targetResult[id]];
          if (!node) {
            return;
          }
          firstGruntRgihtSelf(node, 1); //ownResult1
          firstGruntRightParent(node); //
          firstGruntRightChildren(node, function(child) {
            //
            var index = ownResultCopy.indexOf(child);
            if (index < 0) {
              ownResultCopy.push(child[id]);
            }
          });
        }
      }

      //
      function firstGruntRgihtSelf(node, icon) {
        node.$isIcon = icon; //
        node.$preIcon = icon; //
        if (showLevel) {
          //
          if (node['$level'] <= showLevel) {
            node.$isExpend = true;
          }
        } else {
          node.$isExpend = true;
        }
      }

      //
      function firstGruntRightParent(node, callback) {
        var parentId = node[pid];
        if (angular.isDefined(parentId) && parentId !== '' && parentId !== null) {
          var parentNode = nodeMap[parentId];
          var len = parentNode[children].length;
          var gouNum = 0;
          var chaNum = 0;
          var wenNum = 0;
          for (var i = 0; i < len; i++) {
            if (angular.isUndefined(parentNode[children][i].$isIcon) || parentNode[children][i].$isIcon === 1) {
              gouNum++;
            } else if (parentNode[children][i].$isIcon === 2) {
              chaNum++;
            } else {
              wenNum++;
            }
          }
          if (gouNum === len) {
            firstGruntRgihtSelf(parentNode, 1); //
          } else if (chaNum === len) {
            firstGruntRgihtSelf(parentNode, 2); //
          } else {
            firstGruntRgihtSelf(parentNode, 3); //
          }
          if (callback) {
            callback(parentNode);
          }
          firstGruntRightParent(parentNode, callback);
        }
      }

      //
      function firstGruntRightChildren(node, callback) {
        if (isLeaf(node)) {
          //
          var children = node[$scope.children];
          var len = children.length;
          for (var i = 0; i < len; i++) {
            firstGruntRgihtSelf(children[i], node.$isIcon); //firstGruntRightSelf
            if (callback) {
              callback(children[i]);
            }
            firstGruntRightChildren(children[i], callback);
          }
        }
      }

      //icon
      function getRightIcon(item) {
        switch (item.$isIcon) {
          case 1:
            return 'icon-correct'; //
          case 2:
            return 'icon-error'; //
          case 3:
            return 'icon-query'; //
          case undefined:
            return initShow === undefined || initShow === 'true' ? 'icon-correct' : 'icon-error'; //initShowtrue
        }
      }

      //idbranchNobranchNamemenuIdmenuCaption
      function showField(item) {
        var fieldStr1 = '';
        //
        if (onlyShowCaption) {
          for (var i = 0; i < fidldArry.length; i++) {
            fieldStr1 = fieldStr1 + ' ' + item[fidldArry[i]];
          }
        } else {
          fieldStr1 = item[fidldArry[1]];
        }
        item['fieldStr'] = fieldStr1;
        return item['fieldStr'];
      }

      //
      function isLeaf(item) {
        return angular.isDefined(item[children]) && item[children] !== null && item[children].length > 0;
      }

      //,
      function loopTree(callback) {
        for (var i = 0; i < nodeList.length; i++) {
          //treeData
          callback(nodeList[i]);
        }
      }

      //
      function sortNumber(a, b) {
        return a - b;
      }
    }
  };
}

module.exports = rightTree;



