var app = angular.module('app');

/**
 * IDName
 */

app.filter("tenantIdToName", function () {
  return function (tenantId) {
    var output = tenantId;
    var branch_list = JSON.parse(sessionStorage.getItem("allBranchListData"));
    if(branch_list != null){
      angular.forEach(branch_list, function (item) {
        if (item.branchCode == tenantId && item.branchName.length != 0){
          output = item.branchName;
        }
      });
    }
    return output;
  }
});
