/**
 * 
 */
app.filter('timeStampToDate', function () {
  return function (param) {
    if (!param) {
      return '';
    }
    var date = new Date(parseInt(param) * 1000).toLocaleString().replace(/(\/)/g,'-');
    return (date.split(' ')[0]) || '--';
  }
});

/**
 * 
 */
app.filter('timeStampToTime', function () {
  return function (param) {
    if (!param || param == '0') {
      return '--';
    }
    var date = new Date(param * 1000); //10*1000131000
    var Y = date.getFullYear() + '-';
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
    var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
    var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
    var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
    var s = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()); 
    return Y + M + D + h + m + s;
  }
});

/**
 * 
 */
app.filter('QAAnswerContent', function () {
  return function (data) {
    var answerContent = '';
    try {
      answerContent = JSON.parse(data);
    } catch (error) {
      console.log('formatAnswerContent err', error);
    }
    if (answerContent[0] && answerContent[0].type == 'text') {
      return answerContent[0].body.replace(/\n/g, '') || '';
    } else if (answerContent[0].type == 'img') {
      return answerContent[0].body.link || '';
    } else {
      return '';
    }
  }
});

