app.filter('formatViewpointPkgPrice', function() {
  return function (param) {
    var month = param.month_price != 999999999 ? (param.month_price * 1).toFixed(2) + '/' : '';
    var quarter = param.quarter_price != 999999999 ? (param.quarter_price * 1).toFixed(2) + '/' : '';
    var halfYear = param.half_year_price != 999999999 ? (param.half_year_price * 1).toFixed(2) + '/' : '';
    var year = param.year_price != 999999999 ? (param.year_price * 1).toFixed(2) + '/' : '';
    return month + quarter + halfYear + year;
  }
})

