/*  controller*/

require('./style.less');

var modalOptions = require('jsDir/modal_options'),
  operatorTempl = require('./operatorAccredit.html'),
  func = require('func');

operatorAccreditCtrl.$inject = ['modalService', 'operatorAccreditService'];

function operatorAccreditCtrl(modalService, operatorAccreditService) {
  var vm = this;

  /**/
  vm.operatorAccreditFunc = operatorAccreditFunc;


  /**/
  function operatorAccreditFunc(data, callBack) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '';
    options.template = operatorTempl;
    options.size = 'md';
    options.controller = operatorAccreditController;
    options.modalParams = data;  //
    options.confirmCallback = function (result) {
      angular.isFunction(callBack) && callBack(result);
    };
    modalService.openModal(options);
  }

  /*controller*/
  function operatorAccreditController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, isShowCancelBtn, confirmBtnText, saveBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;

    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.modalTime = modalTime;
    ra.isShowCancelBtn = isShowCancelBtn;
    ra.confirmBtnText = confirmBtnText;
    ra.saveBtnText = saveBtnText;

    ra.branch = modalParams.branch;   //
    ra.branchNo = modalParams.branchNo;
    ra.operatorNo = modalParams.operatorNo;
    ra.name = modalParams.name;
    ra.dataMulList = modalParams.branch;  //
    ra.dictsType = [{"code":"1","value":""},{"code":"2","value":""},{"code":"3","value":""}]; //

    ra.operatorSelfInfo = [];   //

    ra.branchInfoData = [];   //
    ra.operatorBranchInfo = [];   //
    ra.addBranchResultList = [];    //
    ra.delBranchResultList = [];    //
    ra.showBranchId = true;   //id

    ra.roleInfoData = [];   //
    ra.operatorRoleInfo = [];   //
    ra.addRoleResultList = [];    //
    ra.delRoleResultList = [];    //

    ra.addBranchRights = '';  //
    ra.delBranchRights = '';  //
    ra.addRoleRights = '';  //
    ra.delRoleRights = '';  //

    ra.changeRightBranch = changeRightBranch;
    ra.changeRight = changeRight;
    ra.cancel = cancel;
    ra.close = close;
    ra.submit = submit;

    initConfigData();

    //
    function initConfigData() {
      getBranchInfo();
      getRoleInfo();
      getOperatorAuthority();
    }

    //
    function getBranchInfo() {
      operatorAccreditService.branchQry(
        {
          deepth: -1,
          headers: {'enBranchNo': ra.branchNo}
        },
		ra.branchNo,      //requestHeadersenBranchNo
        function (result) {
          ra.branchInfoData = result.data;
        }
      );
    }

    //
    function getRoleInfo() {
      var branchNoList = [];
      branchNoList.push(ra.branchNo);
      operatorAccreditService.roleQry(
        {
          roleId: 0,
          branchNo: ra.branchNo,
          branchNoList: branchNoList,
          roleName: '',
          roleType: '',
          pageNo: 0,
          pageSize: 0
        },
        function (result) {
          handleRoleData(result.data);
        }
      );
    }

    //
    function handleRoleData(data) {
      var roleInfoDataCopy = [];    //
      for (var i = 0; i < data.length; i++) {
        var jsonObj = {
          "roleId": 0,
          "roleType": "",
          "roleName": "",
          "children": []
        };
        data[i].roleName = data[i].roleId + ' ' + data[i].roleName;
        var index = func.isContain(roleInfoDataCopy, data[i]);
        if (index != null) {      //data[i]
          roleInfoDataCopy[index].children.push(angular.copy(data[i]));
        } else {          //data[i]
          for (var j = 0; j < ra.dictsType.length; j++) {
            if (ra.dictsType[j].code == data[i].roleType) {
              jsonObj.roleName = data[i].roleType + ' ' + ra.dictsType[j].value;
            }
          }
          //
          jsonObj.roleType = data[i].roleType;
          jsonObj.roleId = parseInt((Math.random() + 1) * 100000);//6
          console.log('Number:' + jsonObj.roleId);
          jsonObj.children.push(data[i]);
          roleInfoDataCopy.push(angular.copy(jsonObj));
        }
      }
      ra.roleInfoData = roleInfoDataCopy;
      console.log(ra.roleInfoData);
    }

    //
    function getOperatorAuthority() {
      operatorAccreditService.operatorQry(
        {
          operatorNo: ra.operatorNo
        },
        function (result) {
          ra.operatorSelfInfo = result.data;
          //
          if (ra.operatorSelfInfo[0].branchRights != null) {
            ra.operatorBranchInfo = tempArray(ra.operatorSelfInfo[0].branchRights, '1');
          }
          //
          if (ra.operatorSelfInfo[0].roleRights != null) {
            ra.operatorRoleInfo = tempArray(ra.operatorSelfInfo[0].roleRights, '1');
          }
        }
      );
    }

    //
    function changeRightBranch(addResult, delResult, item, root) {
      //
	  addResult = addResult.sort(sortNumber);
	  delResult = delResult.sort(sortNumber);
      //
      angular.forEach(addResult, function (item, index) {
        if (item.toString().length == 6) {
          addResult.splice(index, 1);
        }
      });
      angular.forEach(delResult, function (item, index) {
        if (item.toString().length == 6) {
          delResult.splice(index, 1);
        }
      });
      //
      if (addResult.length != 0) {
        var addList = [];
        for (var i = 0; i < addResult[addResult.length - 1]; i++) {
          addList.push(0);
        }
        for (var i = 0; i <= addResult.length; i++) {
          addList[addResult[i] - 1] = 1;
        }
        ra.addBranchRights = addList.join('');
      }
      //
      if (delResult.length != 0) {
        var delList = [];
        for (var i = 0; i < delResult[delResult.length - 1]; i++) {
          delList.push(0);
        }
        for (var i = 0; i <= delResult.length; i++) {
          delList[delResult[i] - 1] = 1;
        }
        ra.delBranchRights = delList.join('');
      }
    }

    //
    function changeRight(addResult, delResult, item, root) {
      //
	  addResult = addResult.sort(sortNumber);
	  delResult = delResult.sort(sortNumber);
      //
      angular.forEach(addResult, function (item, index) {
        if (item.toString().length == 6) {
          addResult.splice(index, 1);
        }
      });
      angular.forEach(delResult, function (item, index) {
        if (item.toString().length == 6) {
          delResult.splice(index, 1);
        }
      });
      //
      if (addResult.length != 0) {
        var addList = [];
        for (var i = 0; i < addResult[addResult.length - 1]; i++) {
          addList.push(0);
        }
        for (var i = 0; i <= addResult.length; i++) {
          addList[addResult[i] - 1] = 1;
        }
        ra.addRoleRights = addList.join('');
      }

      //
      if (delResult.length != 0) {
        var delList = [];
        for (var i = 0; i < delResult[delResult.length - 1]; i++) {
          delList.push(0);
        }
        for (var i = 0; i <= delResult.length; i++) {
          delList[delResult[i] - 1] = 1;
        }
        ra.delRoleRights = delList.join('');
      }
    }

    //
    function setBranchChange() {
      if (ra.addBranchRights.length != 0 || ra.delBranchRights.length != 0) {
        operatorAccreditService.operatorBranchSet(
          {
            addBranchRights: ra.addBranchRights,
            delBranchRights: ra.delBranchRights,
            addRoleRights: ra.addRoleRights,
            delRoleRights: ra.delRoleRights,
            operatorNo: ra.operatorNo,
            status: '0',
            branchNo: 0,
            name: '',
            telephone: '',
            operatorType: '',
            roleRights: '',
            branchRights: '',
            remark: ''
          },
          function (result) {
            setRoleChange();
          }
        );
      } else {
        setRoleChange();
      }
    }

    //
    function setRoleChange() {
      if (ra.addRoleRights.length != 0 || ra.delRoleRights.length != 0) {
        operatorAccreditService.operatorRoleSet(
          {
            addBranchRights: ra.addBranchRights,
            delBranchRights: ra.delBranchRights,
            addRoleRights: ra.addRoleRights,
            delRoleRights: ra.delRoleRights,
            operatorNo: ra.operatorNo,
            status: '0',
            branchNo: 0,
            name: '',
            telephone: '',
            operatorType: '',
            roleRights: '',
            branchRights: '',
            remark: ''
          },
          function (result) {
            func.openToaster('');
            close();
          }
        );
      } else {
        if (ra.addBranchRights.length != 0 || ra.delBranchRights.length != 0) {
          func.openToaster('');
          close();
        } else {
          close();
        }
      }
    }

    //1
    function tempArray(array, perchar) {
      var len = array.length;
      var resultarray = [];
      for (var i = 0; i < len; i++) {
        if (perchar == array[i]) {
          resultarray.push((i + 1));
        }
      }
      return resultarray;
    }

    //
	function sortNumber(a, b) {
	  return a - b
	}

    //
    function submit() {
      setBranchChange();
    }

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }

  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;

}

module.exports = operatorAccreditCtrl;




