/*  module */

require('./style.less');

//
var Routing = require('./route');

// servicecontrollerdirective
var ctrl = require('./controller');
var service = require('./service');
var combineOnlineModalCtrl = require('./modalPopup/combineOnlineModal/controller');
var combineOnlineModalService = require('./modalPopup/combineOnlineModal/service');

//setter
module.exports = angular
  .module('combineCheck', [])
  .config(Routing)
  .service('combineCheckService', service)
  .controller('combineCheckCtrl', ctrl)
  .factory('combineOnlineModalCtrl', combineOnlineModalCtrl)
  .service('combineOnlineModalService', combineOnlineModalService).name;



