/*  module */
require('locale_zh');
var func = require('func');

branchManageCtrl.$inject = ['modalService', 'branchManageService', 'addBranchModalCtrl', 'changeBranchModalCtrl', 'delBranchModalCtrl'];

function branchManageCtrl(modalService, branchManageService, addBranchModalCtrl, changeBranchModalCtrl, delBranchModalCtrl) {
  var vm = this;

  vm.delResult = [];
  vm.addResult = [];
  vm.ownResult = []; //
  vm.selectedBranchInfo = {}; //
  vm.selectedBranchNo = ''; //branchNo
  vm.navToBranch = ''; //
  vm.showBranchId = true; //id

  vm.rightViewData = JSON.parse(sessionStorage.getItem('branchQryData')); //
  vm.modulesViewData = JSON.parse(sessionStorage.getItem('menuTree')); //

  vm.getItemClicked = getItemClicked;
  vm.changeRight = changeRight;
  vm.saveMenuChange = saveMenuChange;
  vm.openAddBranchModalFunc = openAddBranchModalFunc;
  vm.openModBranchModalFunc = openModBranchModalFunc;
	vm.openDelBranchModalFunc = openDelBranchModalFunc;

  //
  function getItemClicked(item) {
    vm.navToBranch = ''; //
    vm.delResult = [];
    vm.addResult = [];
    vm.ownResult = 'change'; //rightTreeownResult'change'ownResult
    vm.selectedBranchNo = item.branchNo;

    branchMenuInfoQry();
    branchCheckInfoQry();
  }

  //
  function changeRight(addResult, delResult) {
    vm.addResult = addResult;
    vm.delResult = delResult;
  }

  //
  function branchMenuInfoQry() {
    branchManageService.MenuInfoQry(
      {
        branchNo: vm.selectedBranchNo,
        deepth: -1,
        ancestorFlag: 1
      },
      function(result) {
        vm.ownResult = result.data;
      }
    );
  }

  // 
  function branchCheckInfoQry() {
    branchManageService.branchInfoQry(
      {
        branchNo: vm.selectedBranchNo,
        ancestorFlag: 1
      },
      function(result) {
        vm.selectedBranchInfo = result.data[0];
      }
    );
  }

  //
  function saveMenuChange() {
    vm.addResult = vm.addResult.sort(sortNumber);
    vm.delResult = vm.delResult.sort(sortNumber);

    console.log(vm.addResult);
    console.log(vm.delResult);

    //
    var delMenu = [];
    for (var j = 0; j < vm.delResult[vm.delResult.length - 1]; j++) {
      delMenu.push(0);
    }
    for (var i = 0; i <= vm.delResult.length; i++) {
      delMenu[vm.delResult[i] - 1] = 1;
    }
    delMenu = delMenu.join('');

    //
    var addMenu = [];
    for (var j = 0; j < vm.addResult[vm.addResult.length - 1]; j++) {
      addMenu.push(0);
    }
    for (var i = 0; i <= vm.addResult.length; i++) {
      addMenu[vm.addResult[i] - 1] = 1;
    }
    addMenu = addMenu.join('');

    if (vm.selectedBranchNo != '') {
      branchManageService.setBranchMenu(
        {
          branchNo: vm.selectedBranchNo,
          addMenuRights: addMenu,
          delMenuRights: delMenu
        },
        function(result) {
          func.openToaster('');
        }
      );
    } else {
      modalService.openAlert('', '');
    }
  }

  //
  function openAddBranchModalFunc() {
    if (vm.selectedBranchInfo.branchNo == undefined) {
      func.openToaster('');
    } else {
      addBranchModalCtrl.openaddBranchModalFunc(vm.selectedBranchInfo, function() {
        vm.selectedBranchNo = ''; //
        getNewAllBranch(); //
      });
    }
  }

  //
  function openModBranchModalFunc() {
    if (vm.selectedBranchInfo.branchNo == undefined) {
      func.openToaster('');
    } else {
      changeBranchModalCtrl.openchangeBranchModalFunc(vm.selectedBranchInfo, function() {
        vm.selectedBranchNo = ''; //
        getNewAllBranch(); //
      });
    }
  }

  //
  function openDelBranchModalFunc() {
    if (vm.selectedBranchInfo.branchNo == undefined) {
      func.openToaster('');
    } else {
      delBranchModalCtrl.opendelBranchModalFunc(vm.selectedBranchInfo, function() {
        vm.selectedBranchNo = ''; //
        getNewAllBranch(); //
      });
    }
  }

  //
  function getNewAllBranch() {
    branchManageService.allBranchQry(
      {
        deepth: -1
      },
      function(result) {
        //
        sessionStorage.setItem('branchQryData', JSON.stringify(result.data));
        getAllbranchList();
      }
    );
  }

  //
  function getAllbranchList() {
    branchManageService.allBranchQry({}, function(result) {
      sessionStorage.setItem('allBranchListData', JSON.stringify(result.data));
      //
      vm.rightViewData = JSON.parse(sessionStorage.getItem('branchQryData')); //
      vm.modulesViewData = JSON.parse(sessionStorage.getItem('menuTree')); //
      vm.navToBranch = vm.selectedBranchInfo.branchNo; //
    });
  }

  //
  function sortNumber(a, b) {
    return a - b;
  }
}

module.exports = branchManageCtrl;



