/*  */

require('./style.less');

//
var Routing = require('./route');

// servicecontrollerdirective
var ctrl = require('./controller');
var service = require('./service');
var viewpointCheckModalCtrl = require('./modalPopup/viewpointCheckModal/controller');
var viewpointCheckModalService = require('./modalPopup/viewpointCheckModal/service');
var viewpointRemarkModalCtrl = require('./modalPopup/viewpointRemarkModal/controller');
var viewpointDetailModalCtrl = require('./modalPopup/viewpointDetailModal/controller');

//setter
module.exports = angular
  .module('viewpointCheck', [])
  .config(Routing)
  .controller('viewpointCheckCtrl', ctrl)
  .service('viewpointCheckService', service)
  .factory('viewpointCheckModalCtrl', viewpointCheckModalCtrl)
  .service('viewpointCheckModalService', viewpointCheckModalService)
  .factory('viewpointRemarkModalCtrl', viewpointRemarkModalCtrl)
  .factory('viewpointDetailModalCtrl', viewpointDetailModalCtrl)
  .name;



