/*  controller*/

require('./style.less');
window.ExcellentExport = require('ExcellentExport'); //
var modalOptions = require('jsDir/modal_options'),
achievementStatisticsDownloadTempl = require('./achievementStatisticsDownload.html');

achievementStatisticsDownloadCtrl.$inject = ['modalService', 'achievementStatisticsDownloadService'];

function achievementStatisticsDownloadCtrl(modalService, achievementStatisticsDownloadService) {
  var vm = this;

  /**/
  vm.openDownModalFunc = openDownModalFunc;

  /**/
  function openDownModalFunc(data) {
    var options = angular.copy(modalOptions);
    options.modalTitle = '';
    options.template = achievementStatisticsDownloadTempl;
    options.size = 'md';
    options.controller = downModalController;
    options.modalParams = data; //
    modalService.openModal(options);
  }

  /*controller*/
  function downModalController($uibModalInstance, $uibModalStack, modalTitle, modalInfo, modalParams, modalTime, cancelBtnText) {
    var ra = this;
    var errorNo = modalParams.error_no;
    ra.modalTitle = modalTitle;
    ra.modalInfo = modalInfo;
    ra.modalParams = modalParams;
    ra.currentSession = ra.modalParams.current_session; // 01
    ra.cancelBtnText = cancelBtnText;
    ra.content = '...0%';
    ra.achievementStatisticsData = '';
    ra.downFlag = true;

    ra.cancel = cancel;
    ra.close = close;

    init();

    function init() {
      // 01
      if (ra.currentSession == 0) {
        achievementStatisticsDownloadService.branchAchievementStatisticQryFunc(
          {
            tenant_id: ra.modalParams.tenant_id,
            branch_no: ra.modalParams.branch_no,
            service_type: ra.modalParams.service_type,
            page_no: ra.modalParams.page_no,
            page_size: ra.modalParams.page_size
          },
          function(response) {
            handleDataListFunc(response.data_list);
            ra.downFlag = false;
            ra.content = '...100%';
          },
          function(err) {
            ra.content = '';
          }
        );
      } else if (ra.currentSession == 1) {
        achievementStatisticsDownloadService.brokerAchievementStatisticQryFunc(
          {
            tenant_id: ra.modalParams.tenant_id,
            branch_no: ra.modalParams.branch_no,
            service_type: ra.modalParams.service_type,
            broker_name: ra.modalParams.broker_name,
            page_no: ra.modalParams.page_no,
            page_size: ra.modalParams.page_size
          },
          function(response) {
            handleDataListFunc(response.data_list);
            ra.downFlag = false;
            ra.content = '...100%';
          },
          function(err) {
            ra.content = '';
          }
        );
      }
    }

    function handleDataListFunc(dataList) {
      ra.achievementStatisticsData = [];
      // 
      if (dataList.length) {
        dataList.forEach(function(item) {
          if (item.achievement_statistics && item.achievement_statistics.length) {
            item.achievement_statistics.forEach(function(per) {
              ra.achievementStatisticsData.push({
                branchName: item.branch_name,
                brokerName: item.broker_name,
                serviceType: per.title,
                onlineNum: per.up_num,
                nosubscribeNum: per.no_subscription_num,
                checkingNum: per.wait_audit_num,
                checkFailNum: per.refuse_num,
                tradePrice: per.total_price
              })
            })
          } else {
            ra.achievementStatisticsData.push({
              branchName: item.branch_name,
              brokerName: item.broker_name
            })
          }
        })
      }
    }
  

    function close() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.close();
    }

    function cancel() {
      closeAll(errorNo, $uibModalStack);
      $uibModalInstance.dismiss('cancel');
    }
  }

  function closeAll(errorNo, $uibModalStack) {
    if (errorNo == '2009') {
      $uibModalStack.dismissAll();
    }
  }

  return vm;
}

module.exports = achievementStatisticsDownloadCtrl;



